.wf-gun {
  height: 100vh;
  background: #000;

  &__header {
    display: flex;
    justify-content: center;
    height: 48px;
    background: #000;
    border-bottom: 1px solid #333;
    width: 100%;

    &-bg {
      background: url(//wf.cdn.gmru.net/wiki/images/9/98/Bg.jpg) center/auto 100% no-repeat;
      height: 250px;
      overflow: hidden;
      position: relative;
      width: 100%;

      &::after {
        background-image: linear-gradient(0deg, #000 0, transparent 35%);
        bottom: 0;
        content: "";
        height: 100%;
        left: 0;
        position: absolute;
        width: 100%;
      }
    }

    img {
      width: 500px;
    }
  }

  &__content {
    color: white;
    margin: 0 auto;
    width: 1260px;
    height: 500px;
    background-color: #191e23;

    &-settings {
      display: flex;
      padding: 33px 43px 22px;
      max-height: 134px;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
      width: 650px;
      margin: 0 auto;

      &__item {
        display: flex;
        align-items: center;
        margin-bottom: 4px;
        height: 26px;

        label {
          margin-left: 6px;
          width: 110px;
        }
      }
    }

    &-fortune {
      &-shadow {
        position: absolute;
        z-index: 2;
        left: 50%;
        top: 63%;
        transform: translate(-50%, -50%);
        border-radius: 10px;
        border: 2px solid #545353;
        width: 1170px;
        height: 201px;
        box-shadow: inset 0 0 7px 0 #545353;
      }

      &-random {
        position: absolute;
        overflow: hidden;
        left: 50%;
        top: 63%;
        transform: translate(-50%, -50%);
        border-radius: 10px;
        width: 1174px;
        height: 203px;
      }

      &-cards {
        position: absolute;
        left: 0;
        top: 0;
        width: max-content;
        height: 100%;
        transition: 5s ease;

        &.without-transition {
          transition: none;
        }
      }

      &-cards > div {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        float: left;
        font-family: sans-serif;
        font-weight: bold;
        font-size: 20px;
        margin-top: 5px;
        margin-left: 5px;
        width: 320px;
        height: 190px;
        text-shadow: 0 0 40px #c30000;
        color: #a9a3a3;
        background: url(https://oir.mobi/uploads/posts/2021-03/1616432910_13-p-zadnii-fon-chernii-14.jpg);
        border-radius: 10px;
        background-size: contain;
        flex-direction: column;

        img {
          height: 100px;
          max-width: 300px;
        }

        span {
          max-width: 279px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }

      &-result {
        position: absolute;
        z-index: 10;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        border: 10px solid transparent;
        border-top: 20px solid #981313;
        filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
      }

      &-start {
        position: absolute;
        top: calc(63% + 150px);
        left: 50%;
        transform: translate(-50%, -50%);
        width: 250px;
        height: 50px;
        color: white;
        font-weight: bold;
        transition: 0.3s ease;
        cursor: pointer;
        background: #981313;
        border: 1px solid #981313;

        &:hover {
          background: #fff;
          color: #000;
        }
      }
    }
  }
}

